/**
 * 递归判断route的meta中是否存在，
 * 如果存在则将结果保存
 * @param  {obj} routes 路由对象
 * @return {arr}        路由对象集合
 */
import { getToken } from '@/common/js/token';
import { routeFilter } from '@/common/js/getters';

// import UTIL from '@/common/js/util'

const getters = {
  /* 用户 */
  // 用户令牌
  token: state => state.user.token || getToken(),
  // 用户头像
  avatar: state => state.user.avatar,
  // 用户姓名
  userName: state => state.user.userName,
  // 用户角色
  roles: state => state.user.roles,

  /* 路由 */
  // 该用户能够进入的所有页面
  permissionRoutes: state => state.permissionRouter.routes,
  // 需要添加到菜单上的路由
  menuRoutes: state => routeFilter(state.permissionRouter.routes),
  // 该用户权限动态加载的页面
  asynchronousRoutes: state => state.permissionRouter.asynchronousRoutes,

  /* 侧边栏收缩 */
  // 侧边栏是否收起标识
  isCollapse: state => state.aside.isCollapse,

  /* 输入框建议值 */
  // 连续箭头函数就是ES2015的多次柯里化的写法
  // 从而实现传递参数
  // 相当于执行了suggestionGetSuggestions()(keys)
  suggestionGetSuggestions: state => key => state.suggestions.suggestion[key],

  /* 自动完成或者下拉列表备选数据 */
  // 获得备选数据对象：obj = {key: [...], ...}
  // 连续箭头函数就是ES2015的多次柯里化的写法
  // 从而实现传递参数
  // 相当于执行了selectionGetSelections()(keys)
  selectionGetSelections: state => keys => {
    const data = state.selections.selections,
          result = {};

    // 如果data中的键存在于传入的keys中
    // 则将该键值对存入结果中
    for (let item in data) {
      if (keys.indexOf(item) > -1) {
        result[item] = data[item];
      }
    }
    return result;
  },

  /* tabs */
  // 所有的tab
  viewTabs: state => state.tabs.viewTabs,

  /* 订单进度概览 */
  // 修改页面是否可见的标识
  scheduleDialogVisible: state => state.schedule.visible,
  // 被点击行的原始数据
  scheduleSourceData: state => state.schedule.data,
  // 表单默认数据
  scheduleEditDefault: state => {
    let schedule = {};

    if (state.schedule.index !== -1) {
      const storeData = state.schedule.data[state.schedule.index];
      // 借用json实现深复制
      // schedule = UTIL.deepClone(storeData);
      schedule = JSON.parse(JSON.stringify(storeData));
    }

    return schedule;
  },
  // 需要查看详情的订单表id数组
  scheduleOrderInfos: state => state.schedule.resultInfos
}

export default getters



