import axios from 'axios';

// 获取订单进度概览信息
// data：被formData序列化的文件主体
// params：随文件发送的参数
export function getScheduleInfos(params) {
  return axios({
    url: '../../schedule/list.do',
    method: 'get',
    params: params
  });
};

// 修改单条订单进度的计划以及实际信息
export function uploadScheduleInfo(formData, params) {
  return axios({
    url: '../../orderSchedule/update.do',
    method: 'post',
    data: formData,
    params: params
  });
};

// 获取订单进度的单条详情
export function getScheduleDetail(params) {
  return axios({
    url: '../../schedule/listDetail.do',
    method: 'get',
    params: params
  });
};

// 根据订单号或者产品名称查找订单进度id
export function getOrderIdList(params) {
  return axios({
    url: '../../schedule/orderIdList.do',
    method: 'get',
    params: params
  });
};



