export const DELETE_TAB = 'DELETE_TAB';

/* schedule.js——订单进度详情 */
export const SCHEDULE_SET_VISIBLE = 'SCHEDULE_SET_VISIBLE';
export const SCHEDULE_SET_DATA = 'SCHEDULE_SET_DATA';
export const SCHEDULE_EDIT_DATA = 'SCHEDULE_EDIT_DATA';
export const SCHEDULE_SET_INDEX = 'SCHEDULE_SET_INDEX';
export const SCHEDULE_GET_INFOS = 'SCHEDULE_GET_INFOS';

/* suggestions.js——输入框建议值 */
