/* selection.js */
export const SELECTION_CREATE_KEY = 'SELECTION_CREATE_KEY';
export const SELECTION_DELETE_KEY = 'SELECTION_DELETE_KEY';
export const SELECTION_UPDATE_KEY = 'SELECTION_UPDATE_KEY';



