import * as components from './components'

// 所有路径均以'/'开头，即使子路径也不例外
// 否则和element-ui的导航菜单不能正确配合
// 表现为第一次点击菜单时对应菜单项不能高亮！！！
// 路由分成两部分加载
// 首先加载公共部分页面（login、404）
// 然后待后台返回roles之后再加载asynchronousRoutes部分
export const constantRoutes = [
  { path: '/login',
    name: 'login',
    component: components.Login
  },
  { path: '/NotFound',
    // 别名
    alias: '/404',
    name: '404',
    component: components.NotFound
  }
]

// 路由元信息meta中添加了几个自定义字段
// 这些信息用于确定权限以及生成侧边栏需要的信息
// roles：可以查看该页面的角色集合
// menuName：侧边栏显示的文本名
// icon：侧边栏显示的图标
// isMenu: 是否是菜单，如果是则添加到侧边栏菜单中，反之不添加
export let asynchronousRoutes = [
  { path: '/',
    name: 'home',
    component: components.Home,
    redirect: '/welcome', // 默认首页
    children: [
            {
              path: '/welcome',
              name: 'welcome',
              component: components.Welcome,
              meta: {
                roles: [],
                isMenu: true,
                icon: 'el-icon-bell',
                menuName: '欢迎'
              }
            },
            {
              path: '/schedule',
              name: 'schedule',
              component: components.Schedule,
              meta: {
                roles: ['admin'],
                isMenu: true,
                icon: 'el-icon-edit-outline',
                menuName: '订单进度管理'
              },
              children: [
                {
                  path: '/schedule/overview',
                  name: 'scheduleOverview', // route的name设置为表格名，方便获取数据
                  component: components.ScheduleOverview,
                  meta: {
                    roles: ['admin'],
                    isMenu: true,
                    icon: 'el-icon-document',
                    menuName: '订单进度概览'
                  }
                },
                {
                  path: '/schedule/search',
                  name: 'scheduleSearch',
                  component: components.ScheduleSearch,
                  meta: {
                    roles: ['admin'],
                    isMenu: true,
                    icon: 'el-icon-document',
                    menuName: '订单进度搜索',
                  }
                }
              ]
            },
            {
              path: '/table',
              name: 'table',
              component: components.Table,
              meta: {
                roles: ['tableEditer'],
                isMenu: true,
                icon: 'el-icon-document',
                menuName: '表格管理'
              },
              children: [
                {
                  path: '/table/orderInfo',
                  name: 'orderInfo', // route的name设置为表格名，方便获取数据
                  component: components.Table,
                  meta: {
                    roles: ['tableEditer'],
                    isMenu: true,
                    icon: 'el-icon-document',
                    menuName: '订单信息管理'
                  }
                },
                {
                  path: '/table/orderSchedule',
                  name: 'orderSchedule',
                  component: components.Table,
                  meta: {
                    roles: ['tableEditer'],
                    isMenu: true,
                    icon: 'el-icon-document',
                    menuName: '生产进度管理',
                  }
                }
              ]
            },
            {
              path: '/excelUpload',
              name: 'excelUpload',
              component: components.ExcelUpload,
              meta: {
                roles: ['excelEditer'],
                isMenu: true,
                icon: 'el-icon-upload',
                menuName: 'EXCEL文件上传'
              }
            },
            {
              path: '/userInfo',
              name: 'userInfo',
              component: components.UserInfo,
              meta: {
                roles: [],
                isMenu: true,
                icon: 'el-icon-info',
                menuName: '个人信息管理'
              }
            },
            {
              path: '/testView',
              name: 'testView',
              component: components.TestView,
              meta: {
                roles: ['visitor'],
                isMenu: true,
                icon: 'el-icon-document',
                menuName: '测试页面'
              }
            }
        ]
  },
  { // 放在最后，否则全部被拦截到'/'
  path: '*',
  redirect: '/'
  }
]

// 对各页面细粒度权限的映射表
export const permissionMap = {
  scheduleOverview: {
    editAll: ['admin'],
    editOverView: ['test1', 'test2']
  }
}



