// 生产进度信息
import Mock from 'mockjs';

// 扩展mock模板
// 生成部件名
Mock.Random.extend({
    parts: function(date) {
        var constellations = ['筒身', '筒尾', '筒头', '灯座'];
        return this.pick(constellations);
    },
    orderStatus: function(date) {
        var constellations = ['未处理', '已排', '已交氧化', '完成'];
        return this.pick(constellations);
    },
    productStatus: function(date) {
        var constellations = [0, 1];
        return this.pick(constellations);
    }
})

// 生产状态数据数组
