// 自动完成备选列表或者筛选框的项
import * as types from '../mutation-types';

const state = {
  selections: {}, // 自动完成或者下拉框候选列表值
  keys: [],
  assistantObj: {}, // 辅助判断是否重复的对象
};

const mutations = {
  // 新建一种列表类别
  [types.SELECTION_CREATE_KEY] (state, payload) {
    const data = payload.data,
          key = payload.key;

    let obj = {},
        result = [];

    // 遍历data中每一条数据的每一个属性
    data.forEach(item => {

      // 如果第一次遍历到就存入结果中
      if (!obj[item[key]]) {
        obj[item[key]] = true;
        result.push(item[key]);
      }
    });
    // 创建key
    state.keys.push(key);
    // 使用es6的计算
    // 创建key对应的非重复数据
    state.selections = {...state.selections, [key]: result};
    // 创建key对应的辅助对象
    state.assistantObj = {...state.assistantObj, [key]: obj};
  },
  // 删除一种列表类别
  [types.SELECTION_DELETE_KEY] (state, key) {
    // 删除key
    state.keys.splice(state.keys.indexOf(key), 1);
    // 删除key对应的非重复数据
    delete state.selections[key];
    // 删除key对应的辅助对象
    delete state.assistantObj[key];
  },
  // 新增已存在列表类别的项
  [types.SELECTION_UPDATE_KEY] (state, payload) {
    const data = payload.data,
          key = payload.key;

    let obj = state.assistantObj[key],
        result = state.selections[key];

    // 遍历data中每一条数据的每一个属性
    data.forEach(item => {

      // 如果第一次遍历到就存入结果中
      if (!obj[item[key]]) {
        obj[item[key]] = true;
        result.push(item[key]);
      }
    });
    // 更新key对应的非重复数据
    state.selections[key] = result;
    // 更新key对应的辅助对象
    state.assistantObj[key] = obj;
  }
};

const actions = {
  // 增加备选列表数据
  addSelections({ commit, state }, payload){
    const data = payload.data,
          keys = payload.keys;

    let commitPayload = {};
    // 简单地进行校验
    if (keys && keys.forEach && data && data.forEach) {
      // 循环传入的keys，分别对每个key进行判断
      keys.forEach( key =>{
        commitPayload = {
          data: data,
          key: key
        }
        // 如果已经存在该key，则执行update操作
        // 否则执行create操作
        if (state.keys.indexOf(key) > -1) {
          commit(types.SELECTION_UPDATE_KEY, commitPayload)
        } else {
          commit(types.SELECTION_CREATE_KEY, commitPayload)
        }
      });
    }
  },
  // 删除备选列表数据
  deleteSelections({ commit, state }, payload){
    const keys = payload;

    // 简单地进行校验
    if (keys && keys.forEach) {
      keys.forEach( key => {
        if (state.keys.indexOf(key) > -1) {
          commit(types.SELECTION_DELETE_KEY, key);
        }
      });
    }
  },
  // 获取备选列表数据
  getSelections({ state }, keys){
    const result = {};

    if (keys && keys.forEach) {
      keys.forEach( key => {
        // 使用getKeyVals获得数据
        result[key] = state.selections[key];
      });
    }

    return result;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
}




