// 订单信息
import Mock from 'mockjs';

// 扩展mock模板
// 生成部件名
Mock.Random.extend({
    parts: function(date) {
        var constellations = ['筒身', '筒尾', '筒头', '灯座'];
        return this.pick(constellations);
    },
    orderStatus: function(date) {
        var constellations = ['未处理', '已排', '已交氧化', '完成'];
        return this.pick(constellations);
    },
    productStatus: function(date) {
        var constellations = [0, 1];
        return this.pick(constellations);
    }
})

// 订单信息列表
let suggestion = {
  id: [],
  orderNo: [],
  productName: [],
  orderAmount: [],
  stockAmount: [],
  spareParts: [],
  planAmount: [],
  orderDate: [],
  requiredDate: [],
  replyDate: [],
  purchaseRequirement: [],
  materialCoding: [],
  materialModel: [],
  orderStatus: [],
  remark: [],
  exceptionReason: [],
  orderType: [],
  price: []
};

// 得到100个数据
for (let i = 0; i < 100; i++) {
  suggestion.id.push(Mock.Random.guid());
  suggestion.orderNo.push('PO' + Mock.Random.string('number', 8));
  suggestion.productName.push(Mock.Random.string('upper', 2) + Mock.Random.parts() + 'S' +
                Mock.Random.integer(1, 8));
  suggestion.orderAmount.push(100 * Mock.Random.integer(1, 30));
  suggestion.stockAmount.push(Mock.Random.integer(1, 100));
  suggestion.spareParts.push(Mock.Random.integer(1, 50));
  suggestion.planAmount.push(100 * Mock.Random.integer(1, 30));
  suggestion.orderDate.push(Mock.Random.date());
  suggestion.requiredDate.push(Mock.Random.date());
  suggestion.replyDate.push(Mock.Random.date());
  suggestion.purchaseRequirement.push(Mock.Random.cparagraph(0, 20));
  suggestion.materialCoding.push(Mock.Random.string('number', 3) + '.' +
                  Mock.Random.string('number', 3) + '.' +
                  Mock.Random.string('number', 3) + '.' +
                  Mock.Random.string('number', 3));
  suggestion.materialModel.push(Mock.Random.cparagraph(4));
  suggestion.orderStatus.push(Mock.Random.orderStatus());
  suggestion.remark.push(Mock.Random.cparagraph(0, 20));
  suggestion.exceptionReason.push(Mock.Random.cparagraph(0, 20));
  suggestion.orderType.push('白件');
  suggestion.price.push(Mock.Random.float(0, 10, 2, 2));
}

export { suggestion };



