import * as types from '../mutation-types';
import { getOrderIdList } from '@/api/schedule';
import { Message } from 'element-ui';

const state = {
  visible: false, // 表单是否可见
  data: [], // 原始数据
  index: -1, // 被编辑的那条订单进度的索引
  resultInfos: [] // 需要查看详情的订单表id数组
};

const mutations = {
  // 设置是否打开修改界面窗口
  [types.SCHEDULE_SET_VISIBLE] (state, boolean) {
    state.visible = boolean;
  },
  // 设置原始数据
  [types.SCHEDULE_SET_DATA] (state, sourData) {
    state.data = sourData;
  },
  // 修改一条原始数据
  [types.SCHEDULE_EDIT_DATA] (state, payload) {
    // 深度复制
    const schedule = JSON.parse(JSON.stringify({...payload}));

    state.data.splice(state.index, 1, schedule);
  },
  // 设置被编辑数据的索引
  [types.SCHEDULE_SET_INDEX] (state, index) {
    state.index = index;
  },
  // 设置需要查看详情的订单表id数组
  [types.SCHEDULE_GET_INFOS] (state, Infos) {
    state.resultInfos = Infos;
  }
};

const actions = {
  // 分别关闭和打开订单进度修改页面
  // commit、state：结构赋值
  closeEditDialog({ commit }) {
    commit(types.SCHEDULE_SET_VISIBLE, false);
  },
  // 点击编辑按钮触发
  openEditDialog({ commit }, index) {
    // 设置被选中元素的索引
    commit(types.SCHEDULE_SET_INDEX, index);
    // 设置表单弹窗打开
    commit(types.SCHEDULE_SET_VISIBLE, true);
  },
  setSourceData({ commit }, payload) {
    commit(types.SCHEDULE_SET_DATA, payload);
  },
  updateInfo({ commit }, payload) {
    commit(types.SCHEDULE_EDIT_DATA, payload);
  },
  // 设置需要查看详情的订单表id数组
  // 等价于发起了展示结果的请求
  // payload: 请求参数 { orderNo: xxx, productName: xxx, orderId: xxx }
  getOrderInfos({ commit }, payload) {
    getOrderIdList(payload).then(res => {
      const infos = res.data.infos;

      commit(types.SCHEDULE_GET_INFOS, infos);
    }, err => {
      Message({
        type: 'error',
        message: '查询失败，请重试或者联系管理员...'
      })
    });
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
}




