import * as types from '../mutation-types';
import { getSuggestions } from '@/api/suggestions';
import { Message } from 'element-ui';

const state = {
  suggestion: {}, // 自动完成或者下拉框候选列表值
  keys: []
};

const mutations = {
  // 新建一种列表类别，同时存入对应的建议值
  [types.SUGGESTIONS_SET_KEY] (state, payload) {
    const suggestions = payload.suggestions,
          key = payload.key;

    state.keys.push(key);
    state.suggestion[key] = suggestions;
  }
};

const actions = {
  // 增加备选列表数据
  getSuggestions({ commit, state }, key){
    // 如果key不存在，则对后台发起数据获取请求
    if (state.keys.indexOf(key) === -1) {
      // 请求数据成功后存入store中，否则使用message提示错误
      getSuggestions({key: key}).then(res => {
        let suggestions = res.data.value;

        // 对返回的数据做一些处理
        // 使得符合element-ui的数据要求
        suggestions = suggestions.map(item => {
          return {value: item};
        });

        const commitPayload = {
          suggestions: suggestions,
          key: key
        };

        commit(types.SUGGESTIONS_SET_KEY, commitPayload);
      }, err => {
        Message({
          type: 'error',
          message: '获取输入框建议值失败，请手动输入或者联系管理员...'
        });
      });
    }
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
}



